.call-banner {
    max-width: 1360px;
    height: 420px;
    background-image: url(../img/call-banner.png);
    background-position: right;
    background-repeat: no-repeat;
    background-size: cover;
    margin: auto;

    @media screen and (max-width: 1360px) {
         max-width: initial;
    }

    &:before {
        content:'';
        position: absolute;
        width: 100%;
        height: 420px;
        background: rgb(227,243,248);
        background: linear-gradient(90deg, rgba(227,243,248,1) 0%, rgba(227,243,248,1) 31%, rgba(227,243,248,0.8) 44%, rgba(227,243,248,0) 58%, rgba(227,243,248,0) 100%);
        @media screen and (max-width: 720px) {
            width: 100%;
            background: none;
            background-color: rgba(0, 0, 0, 0.6);
        }
    }

    &__content {
        align-self: center;
        @media screen and (max-width: 720px) {
            width: 100%;
        }
    }
    &__title {
        font-size: 48px !important;
        line-height: 56px !important;
        font-weight: 500 !important;
        padding-top: 0 !important;
        span {
            font-weight: 900;
            color: $red;
        }

        @media screen and (max-width: 720px) {
            font-size: 30px !important;
            line-height: 35px !important; 
            color: #fff;
        }
    }

    .call-checkbox {
        margin-top: 30px;
        max-width: 450px;
        display: block;

        @media screen and (max-width: 720px) {
            font-size: 14px;
            color: #fff;
        }

        input {
            margin-right: 15px;
            align-self: flex-start;
        }
    }
}